.clientes_section_container {
  padding: 4vh 6vw;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: 4vh;
}

.image_container img {
  width: 100%;
 
  object-fit: cover;
  max-width: 500px;
  border-radius: 8px;
  padding: center;
}

.clientes_container {
  display: flex;
  flex-direction: column;
  gap: 2vh;
}

.clientes_title {
  font-family: var(--fuente-cuerpo);
  font-size: 39px;
  color: var(--primary-900);
  margin-bottom: 1.5vh;
  text-align: left;
}

.client_container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 1rem;
  flex-wrap: nowrap;
}

.client_image {
  width: 188px;
  height: auto;
  object-fit: contain;
}

.body_25 {
  font-family: var(--fuente-cuerpo);
  font-weight: 300;
  font-size: 25px;
  color: #333;
}

